<template>
  <v-card elevation="0" tile :color="bg_color">
    <v-container class="py-0">
      <v-row justify="center">
        <v-card elevation="0" tile class="px-0" width="1200" :color="bg_color">
          <v-fab-transition>
            <v-btn :color="theme_color" dark absolute top left fab x-large>
              <v-icon color="white">{{ main_icon }}</v-icon>
            </v-btn>
          </v-fab-transition>
          <v-card-text>
            <v-list class="mb-10" :color="bg_color">
              <v-subheader class="px-0 mx-0 mt-5">
                <v-row justify="center" class="px-0 mx-0">
                  <v-col cols="4">
                    <span :class="theme_text_color + ' title'">
                      Contact Me
                    </span>
                  </v-col>
                  <v-spacer></v-spacer>
                </v-row>
              </v-subheader>
              <v-row justify="center" class="mx-0 px-0">
                <v-col
                  v-for="(item, index) of contact"
                  :key="index"
                  align-self="center"
                  align="center"
                >
                  <v-btn
                    @click="contact_func(index)"
                    icon
                    class="my-5"
                    x-large
                    :href="
                      index == 0 ? 'mailto: zhouchuanlong1988@gmail.com' : ''
                    "
                  >
                    <v-icon x-large>
                      {{ item.icon }}
                    </v-icon>
                  </v-btn>

                  <br />
                  <a
                    v-if="index == 0"
                    href="mailto: zhouchuanlong1988@gmail.com"
                    style="color: grey"
                  >
                    <span>
                      {{ item.text }}
                    </span>
                  </a>
                  <span v-else>
                      {{ item.text }}
                  </span>
                </v-col>
              </v-row>
            </v-list>
          </v-card-text>
        </v-card>
      </v-row>
      <v-divider></v-divider>
    </v-container>
  </v-card>
</template>

<script>
export default {
  name: "education",
  data: () => ({
    main_icon: "mdi-email",
    bg_color: "white",
    theme_color: "lime darken-2",
    theme_text_color: "lime--text text--darken-2",
    contact: [
      {
        text: "zhouchuanlong1988@gmail.com",
        icon: "mdi-gmail",
      },
      {
        text: "+45 50108195",
        icon: "mdi-cellphone",
      },
      {
        text: "Havnevej 22, Roskilde, Denmark",
        icon: "mdi-map-marker",
      },
    ],
  }),
  methods: {
    contact_func(index) {
      if (index == 2) {
        window.open(
          "https://www.google.dk/maps/place/Havnevej+22,+4000+Roskilde/"
        );
      }
    },
  },
};
</script>
